.validation{padding:2rem;height:100vh;width:100vw;background:url(/static/media/background-validate.44d00078.jpg) no-repeat center;background-size:cover;position:relative}@media(min-width: 767px){.validation{padding:0}}.validation__container{width:90%;height:80%;padding:3rem;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);border-radius:.5rem;-webkit-backdrop-filter:blur(7px);backdrop-filter:blur(7px);background:rgba(255,255,255,.3);display:flex;flex-direction:column;justify-content:space-around}@media(min-width: 767px){.validation__container{max-width:500px;height:600px;padding:4rem}}.validation__success{color:#0bab64;font-size:2rem;text-align:center}.validation__text,.validation__underline{font-size:1.1rem}.validation__bold,.validation__product{font-weight:bold}.validation__underline{text-decoration:underline}.validation__product{margin-left:2rem}.validation__button{margin-top:2rem;padding:.7rem 2.5rem;background:#0e0e0e;color:#fff;border:solid 3px transparent;border-radius:.5rem;font-size:1.2rem;transition:.3s ease}.validation__button:hover{color:#0e0e0e;background:transparent;border:solid 3px #0e0e0e}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote::before,blockquote::after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}a,del,ins{text-decoration:none}a{color:inherit}label,button{cursor:pointer}input,button{outline:0}body{font-family:"Lato",sans-serif;overflow-x:hidden}a:hover{color:#fff}
